import React from "react";

const Showcase = () => {
  const projects = [
    {
      title: "Task Management",
      category: "Web dev & UI Design",
      image:
        "https://cdn.prod.website-files.com/6757eead5dc653562d488050/67585c225994163eddbaa9a0_a-man-standing-on-the-street-looking-at-his-cell-phone-SVwXm_hX4Wg.jpg",
      link: "/projects/dynamic-structures",
    },
    {
      title: "Fitness  Application",
      category: "Mobile App dev & UI Design",
      image:
        "https://cdn.prod.website-files.com/6757eead5dc653562d488050/67585c45ee4aa7c49913ffa5_a-city-street-with-tall-buildings-and-a-clock-tower-Z9xPTZfRvNw.jpg",
      link: "/projects/visionary-framework",
    },
    {
      title: "Hospital Management System",
      category: "Web dev & UI Design",
      image:
        "https://cdn.prod.website-files.com/6757eead5dc653562d488050/67585c6109c882e84710569e_a-room-with-three-circular-windows-and-a-couch-nqBD2KSAAoU.jpg",
      link: "/projects/design-revolution",
    },
  ];


  const items = [
    {
      title: 'WE DELIVER CREATIVE PROJECTS',
      subtitle: 'OUR DIGITALISED DESIGN TEAM TO ELEVATE',
    },
    {
      title: 'WE PARTNER CLOSELY WITH BRAINSTORM',
      subtitle: 'ENSURING DESIGNS ALIGN WITH ALL VISION AND GOALS',
    },
    {
      title: 'WE ARE BOTH INNOVATIVE AND FUNCTIONAL',
      subtitle: 'THROUGH CONTINUOUS FEEDBACKS AND REVISIONS',
    },
  ];

  return (
    <>
      <section className="py-16 bg-gray-100  w-full bg-[#04081C]">
        <div className="w-full mx-auto px-6">
          <div className="flex justify-between items-center mb-12">
            <h2 className="text-4xl font-bold">
              OUR CREATIVE <br />
              <span className="text-blue-500">showcase</span>
            </h2>
            <a
              href="/project"
              className="bg-blue-500 text-white px-6 py-3 rounded-lg flex items-center gap-2 transition hover:bg-blue-600"
            >
              <span>ALL CASE STUDIES</span>
              <img
                src="https://cdn.prod.website-files.com/6757e4a58bda42b7389bc8e0/67585a489813ca576fb1579b_direction-right%202.svg"
                alt="Arrow Right"
                className="w-5"
              />
            </a>
          </div>

          <div className="relitive gap-8">
            {projects.map((project, index) => (
              <a
                key={index}
                href={project.link}
                className="block bg-white shadow-lg rounded-lg overflow-hidden transition transform hover:scale-105"
              >
                <img
                  src={project.image}
                  alt={project.title}
                  className="w-full h-[50vh] object-cover"
                />
                <div className="p-6 absolute bottom-12 left-6">
                  <button>{project.category}</button>
                  {/* <p className="absolute text-sm text-gray-500 uppercase"></p> */}
                  <h3 className=" text-xl font-semibold mt-2">{project.title}</h3>
                </div>
              </a>
            ))}
          </div>
        </div>
      </section>







      <div className="flex  bg-black text-white h-screen gap-x-2">
        {/* Left Section: Button */}
        <div className="w-1/3">
          <button className="bg-neutral-800 text-white px-6 py-2 rounded-2xl font-semibold">
            WORKING METHODS
          </button>
        </div>

        {/* Right Section: Content */}
        <div className=" flex flex-col gap-8 w-2/3">
          {/* Heading */}
          <h1 className="text-5xl font-bold leading-tight">
            OUR TEAM OF CREATIVE PROFESSIONAL
            INFUSES CREATIVITY AND EXPERTISES
            INTO EVERY PROJECT WE UNDERTAKE.
          </h1>
          <div className=" flex gap-8  w-full ">

            <div className="space-y-6 w-2/3 ">
              <div className="bg-black text-white flex flex-col items-start py-10">
                <div className="relative pl-10">
                  {items.map((item, index) => (
                    <div
                      id="creative-projects"
                      className={`on-scroll-main-content-wrapper _01 transition-opacity duration-500 ${true ? "opacity-100" : "opacity-0"
                        }`}
                      style={{ color: "rgb(190, 191, 195)" }}
                    >
                      <div className="on-scroll-main-content-wrap">
                        <div
                          className="dot-wrapper _01"
                          style={{ backgroundColor: "rgb(126, 126, 126)" }}
                        ></div>
                        <div className="on-scroll-title">{item.title}</div>
                      </div>
                      <div
                        className="  border-l border-dashed h-16 ml-1 mb-1"
                        style={{ borderColor: "rgb(126, 126, 126)" }}
                      >
                        <div className="on-scroll-description px-3">
                          {item.subtitle}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>

            {/* Right Section: Image */}
            <div className=" w-1/3">
              <div className="w-64 h-64 bg-gradient-to-br from-blue-500 to-blue-800 rounded-2xl flex flex-col justify-between p-4">
                <span className="text-xs font-bold">INDEX®</span>
                <span className="text-xs">BASED IN CALIFORNIA, USA</span>
                <span className="text-xs">WE WORK SINCE 2012 ©2024</span>
              </div>
            </div>
          </div>
        </div>
        {/* Bullet Points */}
      </div>

    </>
  );
};

export default Showcase;
